<template>
  <v-card>
      <v-container fluid grid-list-md class="grey lighten-3">
        <v-layout row wrap>
          <v-flex d-flex xs12 sm12 md6 v-if="!showBigMap">
            <v-layout row wrap>
              <v-flex d-flex>
                <v-card>
                  <v-card-media :src="imgUrl+ '/' + vessel.MMSI + '.jpg'" height="200px">
                  </v-card-media>
                  <div class="caption  ma-3">
                    <!--h3 class="headline mb-0">{{ vessel.Ship_name }}</h3-->
                    <div>MMSI: {{ vessel.MMSI }}</div>
                    <div class="text-gray">SOG: {{ vessel.SOG }} knots - COG: {{vessel.COG}}</div>
                    <div>Latitude: {{ vessel.Latitude }} Longitude: {{ vessel.Longitude }}</div>
                    <div>Last updated: {{new Date(vessel.Time_stamp).toLocaleString()}}</div>
                    <v-divider class="mt-2"></v-divider>
                  </div>

                  <v-switch label="Live Tracking"
                            v-model="liveTracking"
                            color="green"
                            hide-details
                            class="ml-3 mt-0 mb-0"
                  ></v-switch>
                  <v-switch label="Historical Track"
                            v-model="historicalTracking"
                            color="red"
                            hide-details
                            class="ml-3 mt-0 mb-0"
                  >
                  </v-switch>
                  <transition name="fade">
                  <v-card-title primary-title class="mt-0">
                    <data-time-picker @DateTimeChange="fetchHistoricalPath"></data-time-picker>
                  </v-card-title>
                  </transition>
                </v-card>
              </v-flex>
            </v-layout>
          </v-flex>
          <v-flex d-flex xs12 sm12 md6 id="map">
            <v-card>
              <v-flex d-flex xs12>
                <sea-map :vessel="vessel" @showBigMap="showBigMap=!showBigMap" v-if="isSelected"></sea-map>
              </v-flex>
            </v-card>
          </v-flex>
        </v-layout>
      </v-container>
  </v-card>
</template>

<script>
  import SeaMap from '../SeaMap.vue'
  import DataTimePicker from '../DateTimePicker.vue'
  import moment from 'moment'

  export default {
    components: {
      DataTimePicker,
      SeaMap},
    name: 'vessel-view',
    props: {
      vessel: null
    },
    data: function () {
      return {
        imgUrl: 'static/vessel-images',
        showBigMap: false,
        timeTravel: false
      }
    },
    computed: {
      selectedVessel: function () {
        return this.$store.getters.selectedVessel
      },
      isSelected: function () {
        if (this.vessel && this.selectedVessel) {
          return this.selectedVessel.MMSI === this.vessel.MMSI
        }
        return false
      },
      historicalTracking: {
        get () { return this.$store.getters.historicalTracking },
        set (value) {
          this.$store.dispatch('setHistoricalTracking', value)
        }
      },
      liveTracking: {
        get () { return this.$store.getters.liveTracking },
        set (value) {
          this.$store.dispatch('setLiveTracking', value)
        }
      }
    },
    watch: {
      'selectedVessel': function () {
        this.fetchHistoricalPath()
      }
    },
    methods: {
      fetchHistoricalPath: function () {
        let date = this.$store.getters.historicalDate
        let time = this.$store.getters.historicalTime
        const t2 = new Date(date + 'T' + time)
        const t1 = moment(t2).subtract(this.$store.getters.pathInMinutes, 'minutes').toDate()
        console.log('t1, t2', t1.toUTCString(), t2.toUTCString)
        console.log('RUN fetch 1')
        this.$store.dispatch('fetchHistoricalVesselPath', { mmsi: this.selectedVessel.MMSI, fromUTC: t1.toUTCString(), toUTC: t2.toUTCString() })
      }
    }
  }
</script>

<style scoped>
  #map {
    max-width: 100%
  }
  .fade-enter-active {
    transition: opacity .5s
  }
  .fade-leave-active {
    transition: opacity 0
  }
  .fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
    opacity: 0
  }
</style>



