<template>
  <v-layout row>
      <v-menu
        lazy
        :close-on-content-click="false"
        v-model="menu"
        transition="scale-transition"
        offset-y
        full-width
        :nudge-right="40"
        max-width="290px"
        min-width="290px"
      >
        <v-text-field class="mr-1 date-time-field"
          slot="activator"
          label="marker date"
          v-model="date"
          prepend-icon="event"
          readonly
          style="max-width:125px"
        ></v-text-field>
        <v-date-picker v-model="date">
        </v-date-picker>
      </v-menu>
      <v-menu
        lazy
        :close-on-content-click="false"
        v-model="menu2"
        transition="scale-transition"
        offset-y
        full-width
        :nudge-right="40"
        max-width="290px"
        min-width="290px"
      >
        <v-text-field class="date-time-field ml-2"
          slot="activator"
          label="marker time"
          v-model="time"
          prepend-icon="access_time"
          readonly
          style="max-width:125px"
        ></v-text-field>
        <v-time-picker v-model="time" format="24hr"></v-time-picker>
      </v-menu>
  </v-layout>
</template>

<script>
  export default {
    name: 'data-time-picker',
    data: () => ({
      menu: false,
      modal: false,
      menu2: false,
      modal2: false,
      expanded: false
    }),
    computed: {
      date: {
        get () { return this.$store.getters.historicalDate },
        set (date) {
          this.$store.dispatch('setHistoricalDate', date)
          this.$emit('DateTimeChanged')
        }
      },
      time: {
        get () { return this.$store.getters.historicalTime },
        set (time) {
          this.$store.dispatch('setHistoricalTime', time)
          this.$emit('DateTimeChanged')
        }
      }
    }
  }
</script>

<style scoped>
  .date-time-field {
    max-width: 155px;
  }
</style>



