<template>
  <div>
  <gmap-map
    ref="seaMap"
    :center="center"
    :zoom="zoom"
    :options="{disableDefaultUI: true}"
    :style=style
    map-type-id="roadmap"
    @dragend="centerToVessel"
  >
    <gmap-marker
      :key="index"
      v-for="(m, index) in markers"
      :position="m.position"
      :clickable="true"
      :draggable="false"
      @click="center=m.position"
      :icon="icon",
      :visible="showLiveTrack"
    ></gmap-marker>
    <gmap-marker
      :key="index"
      v-for="(m, index) in historicalMarkers"
      :position="m.position"
      :clickable="true"
      :draggable="false"
      @click="center=m.position"
      :icon="historicalIcon"
      :visible="showHistoricalTrack"
    ></gmap-marker>
    <gmap-polyline v-if="showLiveTrack" :path="path" :options="polyOptions"></gmap-polyline>
    <gmap-polyline v-if="showHistoricalTrack" :path="historicalPath" :options="polyOptionsHistorical"></gmap-polyline>
  </gmap-map>
    <v-btn
      class="blue" id="maptype"
      @click="showSeaMap=!showSeaMap"
      dark
      small
      absolute
      top
      right
      fab
    >
      <v-icon>{{seaMapIcon}}</v-icon>
    </v-btn>
    <v-btn
    class="green" id="zoomup"
    @click="zoom++"
    dark
    small
    absolute
    bottom
    right
    fab
  >
    <v-icon>add</v-icon>
  </v-btn>
  <v-btn
    class="red" id="zoomdown"
    @click="zoom--"
    dark
    small
    absolute
    bottom
    right
    fab
  >
    <v-icon>remove</v-icon>
  </v-btn>
  <v-btn
    class="deep-orange" id="bigMap"
    @click="showBigMap=!showBigMap"
    dark
    small
    absolute
    top
    left
    fab
  >
    <v-icon>{{bigMapIcon}}</v-icon>
  </v-btn>
  <v-btn
    class="pink" id="followVessel"
    @click="followVessel=!followVessel"
    dark
    small
    absolute
    top
    right
    fab
  >
    <v-icon>{{followVesselIcon}}</v-icon>
  </v-btn>
  <v-btn v-show="showLiveTrack"
    class="green" id="centerVessel"
    @click="followVessel=!followVessel"
    dark
    small
    absolute
    top
    right
    fab
  >
    <v-icon>{{centerVesselIcon}}</v-icon>
  </v-btn>
  <v-btn v-show="showHistoricalTrack"
    class="red" id="centerHistoricalVessel"
    @click="followVessel=!followVessel"
    dark
    small
    absolute
    top
    right
    fab
  >
    <v-icon>{{centerVesselIcon}}</v-icon>
  </v-btn>
  </div>
</template>

<script>
  import * as VueGoogleMaps from 'vue2-google-maps'
  import Vue from 'vue'
  import moment from 'moment'

  Vue.use(VueGoogleMaps, {
    load: {
      key: 'AIzaSyCCFWll4oEjXml1BVeZ3-x1TZphNVx8yko',
      v: '3'
    }
  })

  const SeaMapType = function (google) {
    this.tileSize = new google.maps.Size(256, 256)
    this.maxZoom = 19
    this.getTile = function (coord, zoom, ownerDocument) {
      const div = ownerDocument.createElement('DIV')
      div.style.width = '600px'
      div.style.height = '500px'
      div.style.backgroundImage = 'url(http://opencache.statkart.no/gatekeeper/gk/gk.open_gmaps?layers=sjokartraster&zoom=' + zoom + '&x=' + coord.x + '&y=' + coord.y + ')'
      return div
    }
  }

  export default {
    props: {
      vessel: null
    },
    data () {
      return {
        center: { lat: parseFloat(this.vessel.Decimal_Latitude), lng: parseFloat(this.vessel.Decimal_Longitude) },
        markers: [
          {
            position: { lat: parseFloat(this.vessel.Decimal_Latitude), lng: parseFloat(this.vessel.Decimal_Longitude) },
            icon: this.icon
          }
        ],
        showSeaMap: false,
        zoom: 12,
        showBigMap: false,
        followVessel: true,
        style: 'width: 100%; height: 500px'
      }
    },
    computed: {
      selectedVessel: function () {
        return this.$store.getters.selectedVessel
      },
      seaMapIcon: function () {
        if (this.showSeaMap) {
          return 'layers_clear'
        }
        return 'layers'
      },
      bigMapIcon: function () {
        if (this.showBigMap) {
          this.style = 'width: 100%; height: 600px'
          return 'fullscreen'
        }
        this.style = 'width: 100%; height: 500px'
        return 'fullscreen_exit'
      },
      followVesselIcon: function () {
        if (this.followVessel) {
          return 'lock'
        }
        return 'lock_open'
      },
      centerVesselIcon: function () {
        if (this.followVessel) {
          return 'center_focus_strong'
        }
        return 'center_focus_weak'
      },
      centerHistoricalVesselIcon: function () {
        if (this.followVessel) {
          return 'center_focus_strong'
        }
        return 'center_focus_weak'
      },
      path: function () {
        return this.$store.getters.selectedVesselPath
      },
      historicalPath: function () {
        return this.$store.getters.historicalVesselPath
      },
      pathInMinutes: function () {
        return this.$store.getters.pathInMinutes
      },
      showLiveTrack: function () {
        return this.$store.getters.liveTracking
      },
      showHistoricalTrack: function () {
        return this.$store.getters.historicalTracking
      },
      historicalMarkers: function () {
        const m = this.$store.getters.historicalVesselPath[this.$store.getters.historicalVesselPath.length - 1]
        return [
          {
            position: { lat: m.lat, lng: m.lng }
          }
        ]
      },
      icon: function () {
        if (!window.google) {
          return
        }
        return {
          path: 'M 14.00,7.00 C 19.52,20.87 20.20,24.18 20.00,39.00 19.97,40.89 20.04,43.88 18.83,45.43 17.07,47.65 8.93,47.65 7.17,45.43 5.96,43.88 6.03,40.89 6.00,39.00 5.78,22.61 6.76,22.12 12.00,7.00 12.00,7.00 14.00,7.00 14.00,7.00 Z M 12.00,23.00 C 12.00,23.00 12.00,29.00 12.00,29.00 15.86,26.75 15.86,25.25 12.00,23.00 Z',
          fillColor: parseFloat(this.vessel.SOG) > 20 ? '#388E3C' : '#8BC34A',
          fillOpacity: 0.8,
          anchor: new window.google.maps.Point(13, 20),
          strokeColor: parseFloat(this.vessel.SOG) > 12 ? '#757575' : '#757575',
          strokeWeight: 1.5,
          strokeOpacity: 1,
          scale: 1,
          rotation: parseFloat(this.vessel.COG)
        }
      },
      historicalIcon: function () {
        if (!window.google) {
          return
        }
        return {
          path: 'M 14.00,7.00 C 19.52,20.87 20.20,24.18 20.00,39.00 19.97,40.89 20.04,43.88 18.83,45.43 17.07,47.65 8.93,47.65 7.17,45.43 5.96,43.88 6.03,40.89 6.00,39.00 5.78,22.61 6.76,22.12 12.00,7.00 12.00,7.00 14.00,7.00 14.00,7.00 Z M 12.00,23.00 C 12.00,23.00 12.00,29.00 12.00,29.00 15.86,26.75 15.86,25.25 12.00,23.00 Z',
          fillColor: parseFloat(this.$store.getters.historicalVesselPath[this.$store.getters.historicalVesselPath.length - 1].sog) > 20 ? '#D32F2F' : '#FFCDD2',
          fillOpacity: 0.5,
          anchor: new window.google.maps.Point(13, 20),
          strokeColor: parseFloat(this.$store.getters.historicalVesselPath[this.$store.getters.historicalVesselPath.length - 1].sog) > 12 ? '#757575' : '#757575',
          strokeWeight: 1,
          strokeOpacity: 0.5,
          scale: 1,
          rotation: parseFloat(this.$store.getters.historicalVesselPath[this.$store.getters.historicalVesselPath.length - 1].cog)
        }
      },
      polyOptions: function () {
        if (!window.google) {
          return
        }
        return {
          strokeColor: '#388E3C',
          strokeOpacity: 0.7,
          strokeWeight: 2,
          geodesic: true,
          icons: [{
            icon: {
              path: window.google.maps.SymbolPath.FORWARD_OPEN_ARROW,
              scale: 1.5
            },
            repeat: '100px'
          }]
        }
      },
      polyOptionsHistorical: function () {
        if (!window.google) {
          return
        }
        return {
          strokeColor: '#D32F2F',
          strokeOpacity: 0.5,
          strokeWeight: 2,
          geodesic: true,
          icons: [{
            icon: {
              path: window.google.maps.SymbolPath.FORWARD_OPEN_ARROW,
              scale: 1.5
            },
            repeat: '100px'
          }]
        }
      }
    },
    watch: {
      'selectedVessel': function () {
        console.log('watch: selectedVessel')
        Vue.$gmapDefaultResizeBus.$emit('resize')
      },
      'vessel': function () {
        console.log('vessel update: ', this.vessel.MMSI)
        if (this.vessel.MMSI === this.selectedVessel.MMSI) {
          this.$store.dispatch('addToPath', { lat: parseFloat(this.vessel.Decimal_Latitude), lng: parseFloat(this.vessel.Decimal_Longitude) })
          if (this.followVessel) {
            this.center = { lat: parseFloat(this.vessel.Decimal_Latitude), lng: parseFloat(this.vessel.Decimal_Longitude) }
          }
          console.log('HS', this.historicalMarkers.lat, this.historicalMarkers.lng)
          this.markers = [
            {
              position: { lat: parseFloat(this.vessel.Decimal_Latitude), lng: parseFloat(this.vessel.Decimal_Longitude) }
            },
            {
              position: { lat: this.historicalMarkers.lat || 0, lng: this.historicalMarkers.lng || 0 }
            }
          ]
        }
      },
      'showSeaMap': function () {
        if (this.showSeaMap) {
          this.$refs.seaMap.$mapObject.setMapTypeId('sjokartraster')
        } else {
          this.$refs.seaMap.$mapObject.setMapTypeId('roadmap')
        }
      },
      'showBigMap': function () {
        this.$emit('showBigMap')
        Vue.$gmapDefaultResizeBus.$emit('resize')
      },
      'followVessel': function () {
        console.log('watch: followVessel')
        this.centerToVessel()
      },
      'pathInMinutes': function () {
        console.log('watch: pathInMinutes')
        if (this.vessel.MMSI === this.selectedVessel.MMSI) {
          this.refetchPath()
        }
      }
    },
    methods: {
      centerToVessel () {
        console.log('method: centerToVessel')
        if (this.followVessel) {
          this.$refs.seaMap.$mapObject.setCenter(this.markers[0].position)
        }
      },
      refetchPath () {
        console.log('method: refetchPath')
        const t2 = new Date()
        const t1 = moment(t2).subtract(this.pathInMinutes, 'minutes').toDate()
        this.$store.dispatch('fetchSelectedVesselPath', { mmsi: this.selectedVessel.MMSI, fromUTC: t1.toUTCString(), toUTC: t2.toUTCString() })
      }
    },
    mounted: function () {
      if (this.$refs.seaMap) {
        this.$refs.seaMap.$mapCreated.then(() => {
          this.$refs.seaMap.$mapObject.mapTypes.set('sjokartraster', new SeaMapType(window.google))
        })
      }
    },
    created: function () {
      console.log('created: ', this.vessel.MMSI)
    }
  }
</script>
<style>
  #maptype {
    top: 12px;
    right: 12px;
    z-index: 1;
  }
  #zoomup {
  bottom: 82px;
  right: 12px;
  z-index: 1;
}
#zoomdown {
  bottom: 32px;
  right: 12px;
  z-index: 1;
}
#bigMap {
  top: 12px;
  left: 12px;
  z-index: 1;
}
#followVessel {
  top: 72px;
  right: 12px;
  z-index: 1;
}
#centerVessel {
  top: 130px;
  right: 12px;
  z-index: 1;
}
#centerHistoricalVessel {
  top: 180px;
  right: 12px;
  z-index: 1;
}
</style>



